@font-face { font-family: helioscondc; src: url("fonts/helioscondc.eot"); src: url("fonts/helioscondc.eot?#iefix") format("embedded-opentype"), url("fonts/helioscondc.woff2") format("woff2"), url("fonts/helioscondc.woff") format("woff"), url("fonts/helioscondc.ttf") format("truetype"); font-weight: normal; font-style: normal; }
