html { font-size: 62.5%; }

body { font-family: "Open Sans", serif; font-size: 15px; line-height: 1.4; background: #fff; min-width: 320px; }

a { color: #0086b3; cursor: pointer; transition: color 0.3s; }
a:hover, a:focus, a:visited { text-decoration: none; }
a:hover { color: #006b8f; }

input, button { border-radius: 2px; }

.logo { font-size: 0; display: inline-block; }
.logo__text { color: #fff; font-size: 23px; font-family: "helioscondc-bold", Arial, sans-serif; }
.logo__text span { font-family: "helioscondc", Arial, sans-serif; color: #a4eb4b; }
.logo__img { margin-right: 15px; }
.logo__text, .logo__img { display: inline-block; vertical-align: middle; }

.badge { font-size: 13px; border-radius: 50%; background: #ff340c; text-align: center; width: 25px; height: 25px; padding-top: 6px; }

.checkbox input[type="checkbox"] { top: 2px; }

.form-range > div:first-child { position: relative; }
.form-range > div:first-child:after { content: ""; position: absolute; top: 50%; right: -5px; width: 10px; height: 1px; background: #000; }

.header { background: #30373c; font-size: 0; height: 45px; *zoom: 1; }
.header:after { content: " "; display: table; clear: both; }
.header__item { display: inline-block; vertical-align: middle; font-size: 15px; }
.header__item:first-child { width: 190px; }
.header__item.is-right { float: right; height: 100%; }
.header .search-form { padding-top: 5px; }
.header .logo { padding-top: 7px; }

.search-form { position: relative; }
.search-form__input { background: #282d31; border: 1px solid #1e2225; color: #fff; padding-left: 33px; line-height: 30px; height: 30px; width: 300px; font-size: 14px; transition: border-color 0.3s; display: inline-block; vertical-align: middle; }
.search-form__input::placeholder { color: #414950; }
.search-form__input:focus { border-color: #0f1112; }
.search-form__btn { position: absolute; left: 0; background: transparent; color: #414950; width: 33px; text-align: center; font-size: 15px; line-height: 30px; }
.search-form .more-search { display: inline-block; vertical-align: middle; margin-left: 4px; }
.search-form .dropdown-menu { max-width: 400px; }

.more-search { color: #6c9444; font-size: 22px; }
.more-search:hover { color: #5a7c39; }

.menu { *zoom: 1; height: 100%; text-align: center; font-size: 0; }
.menu:after { content: " "; display: table; clear: both; }
.menu:before { content: ""; display: inline-block; vertical-align: middle; width: 0; height: 100%; }
.menu li { display: inline-block; vertical-align: middle; position: relative; }
.menu li a { color: #d1d2d3; opacity: 0.3; transition: opacity 0.3s, background 0.3s; font-size: 22px; padding: 13px 15px 10px; position: relative; }
.menu li a:hover, .menu li a.is-active { opacity: 1; }
.menu li a:hover { background: #394147; }
.menu li .badge { position: absolute; top: 8px; left: 9px; }
.menu li.is-user a { position: relative; z-index: 3; }
.menu li.is-user:before { content: ""; position: absolute; top: 50%; left: 50%; margin-top: -17px; margin-left: -17px; width: 34px; height: 34px; background: #282d31; border-radius: 50%; }

.badge { font-size: 12px; border-radius: 50%; background: #ff340c; text-align: center; width: 16px; height: 16px; padding-top: 3px; z-index: 5; padding: 3px 0 0; }

.sidebar { position: absolute; top: 45px; left: 0; bottom: 0; width: 205px; background: #37424A; }
.sidebar.is-collapsed { width: 55px; }
.sidebar.is-collapsed .sidebar__in { overflow: inherit; }
.sidebar.is-collapsed .sidebar__link.is-mobile { display: block; }
.sidebar.is-collapsed .sidebar__link span { display: none; }
.sidebar.is-collapsed .sidebar__collapse span { display: none; }
.sidebar.is-collapsed .sidebar__sublist { position: absolute; top: 0; left: 100%; min-width: 120px; margin-left: -5px; box-shadow: 2.1px 2.1px 3px rgba(0, 0, 0, 0.2); }
.sidebar.is-collapsed .sidebar__sublist .sidebar__link { padding-left: 18px; }
.sidebar.is-collapsed .fa-chevron-down { display: none; }
.sidebar__list li { position: relative; }
.sidebar__in { position: relative; height: 100%; padding-top: 14px; overflow: hidden; }
.sidebar__link { position: relative; display: block; padding: 6px 18px; font-size: 15px; color: #afb3b7; }
.sidebar__link:before { display: none; content: ""; position: absolute; top: 50%; right: 0; margin-top: -10px; width: 0px; height: 0px; border-style: solid; border-width: 10px 10px 10px 0; border-color: transparent #fff transparent transparent; }
.sidebar__link:hover, .sidebar__link:focus { color: #FFF; background: #2f383f; }
.sidebar__link.is-active { background: #78b641; color: #fff; }
.sidebar__link.is-active:before { display: block; }
.sidebar__link.is-expanded { background: #293137; }
.sidebar__link.is-expanded .fa-chevron-down { transform: rotateZ(-180deg); }
.sidebar__link.is-mobile { display: none; background: #293137; cursor: pointer; }
.sidebar__link span { padding-left: 14px; }
.sidebar__sublist { display: none; background: #2f383f; transition: 0.3s ease; }
.sidebar__sublist.is-active { display: block; }
.sidebar__sublist .sidebar__link { padding-left: 50px; }
.sidebar__collapse { position: absolute; display: block; left: 0; bottom: 10px; padding-left: 18px; color: #afb3b7; }
.sidebar__collapse:hover { color: #FFF; }
.sidebar__collapse:hover span { opacity: 1; visibility: visible; }
.sidebar__collapse:focus { color: #FFF; }
.sidebar__collapse span { padding-left: 14px; font-size: 15px; opacity: 0; visibility: hidden; transition: 0.3s; }
.sidebar__collapse .fa-arrow-circle-left { font-size: 20px; }
.sidebar .fa { font-size: 17px; }
.sidebar .fa-chevron-down { position: absolute; top: 50%; right: 15px; margin-top: -3.5px; font-size: 7px; transition: transform 0.3s; }
@media only screen and (max-height: 500px) { .sidebar__collapse { position: static; margin: 40px 0 15px; } }

.scroll:hover > .ps-scrollbar-y-rail:hover { background-color: #afb3b7; }
.scroll:hover > .ps-scrollbar-y-rail:hover > .ps-scrollbar-y { background-color: #282D31; }
.scroll:hover.ps-in-scrolling.ps-y > .ps-scrollbar-y-rail > .ps-scrollbar-y { background-color: #282D31; }
.scroll.ps-in-scrolling.ps-y > .ps-scrollbar-y-rail > .ps-scrollbar-y { background-color: #282D31; }
.scroll > .ps-scrollbar-y-rail { right: 0; width: 10px; background: none; border-radius: 0; }
.scroll > .ps-scrollbar-y-rail > .ps-scrollbar-y { width: 10px; border-radius: 0; background-color: #282D31; opacity: 0.5; }

